.howItWorksSection {
  padding: 57px 0px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.desktopList {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 46px;
  margin-bottom: 56px;
}

.desktopList > div:nth-child(2) {
  background: #f9f0e6;
}

.mobileSlider {
  display: none;
  max-width: 300px;
  margin: 24px auto 40px;
  position: relative;
}

.slick-slide {
  display: flex;
  justify-content: center;
}

.customArrow {
  width: 30px;
  height: 30px;
  margin-left: -25px;
  margin-right: -25px;
  z-index: 1;
  display: flex !important;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease;
}

.customArrow:hover {
  opacity: 0.7;
}

.mobileSlider .slick-prev {
  left: -40px;
}

.mobileSlider .slick-next {
  right: -40px;
}

.slick-next {
  right: -50px;
}

.mobileSlider .slick-prev:before,
.mobileSlider .slick-next:before {
  display: none !important;
}

/* Медиа-запросы */
@media (max-width: 768px) {
  .desktopList {
    display: none;
  }

  .mobileSlider {
    display: block;
  }
}

.slick-prev,
.slick-next {
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 400px) {
  .mobileSlider .slick-prev {
    left: -30px;
  }

  .mobileSlider .slick-next {
    right: -30px;
  }
}

@media (min-width: 1465px) {
  .howItWorksSection {
    padding: 50px 100px 50px;
  }

  .desktopList > div:nth-child(2) {
    background: #f9f0e6;
  }
}
